import React from "react";
import {Card, Form, Input, message, Button} from "antd";
import PubSub from 'pubsub-js';

import LikeUploadImg from "../../../components/LikeUploadImg";
import EditPassword from "./edit-password";
import {getUser, saveUser, changeAdminMsg} from '../../../api/adminApi';
import config from "../../../config/config";

export default class AccountSetting extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            account_icon: '',
            editPwdPanelShow: false,
            account: '',
            account_name: '',
            token: '',
        };
        // ref
        this.formRef = React.createRef();
    }

    componentDidMount() {
        // 
        const userObj = getUser();
        this.setState({
            account: userObj.account,
            account_name: userObj.account_name,
            account_icon: userObj.account_icon,
            token: userObj.token,
        }, () => {
            // 
            const {account, account_name} = this.state;
            this.formRef.current.setFieldsValue({
                account, account_name
            });
        });
    }

    // 
    editPassword = () => {
        this.setState({
            editPwdPanelShow: true,
        });
    }
    _hidePwdPanel = () => {
        this.setState({
            editPwdPanelShow: false,
        });
    }

    render() {
        const {account_icon} = this.state;
        const onFinish = values => {
            console.log(values);
            const {token, account_icon} = this.state;
            // 
            changeAdminMsg(token, values.account_name, account_icon).then(result => {
                if (result && result.status === 1) {
                    // 
                    saveUser(result.data);
                    message.success(result.msg);
                    // 
                    PubSub.publish('changeAdminMsg', {});
                }
            }).catch(error => {
                message.error('');
            })
        }
        const formItemLayout = {
            labelCol: {
                sm: {span: 6}
            },
            wrapperCol: {
                sm: {span: 8}
            }
        }

        return (
            <Card title={""}>
                <Form {...formItemLayout} onFinish={onFinish} ref={this.formRef}>
                    <Form.Item label={""} name={"account"}>
                        <Input disabled/>
                    </Form.Item>
                    <Form.Item label={""} name={"account_name"}
                               rules={[{required: true, message: ''}]}>
                        <Input placeholder={""}/>
                    </Form.Item>
                    <Form.Item label={""} name={"account_icon"}>
                        <LikeUploadImg upLoadBtnTitle={""} upLoadName={"admin_avatar"}
                                       upLoadAction={"/api/auth/admin/upload_admin_icon"} successCallBack={(name) => {
                            console.log(name);
                            this.setState({
                                account_icon: name
                            })
                        }} upLoadImg={config.BASE_URL + account_icon}/>
                    </Form.Item>
                    <Form.Item wrapperCol={{xs: {span: 24}}}>
                        <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%'}}>
                            <Button type={"primary"} htmlType={"submit"}></Button>
                            <div style={{marginLeft: 10}}> <span style={{color: 'blue', cursor: 'pointer'}}
                                                                   onClick={this.editPassword}></span></div>
                        </div>
                    </Form.Item>
                </Form>
                <EditPassword visible={this.state.editPwdPanelShow} hideFunc={this._hidePwdPanel}/>
            </Card>
        );
    }
}
