import React from "react";
import {Card} from 'antd';
import ReactEcharts from "echarts-for-react";
import {getBuyCount} from "../../../../api/homeApi";

export default class BuyCount extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
        }
    }

    componentDidMount() {
        this._loadData();
    }

    _loadData = () => {
        getBuyCount().then(result => {
            let tempData = [];
            if (result.status === 1) {
                for (let key in result.data) {
                    tempData.push(result.data[key]);
                }
                // 
                this.setState({
                    data: tempData,
                });
            }
        }).catch(error=>{
            console.log(error);
        });
    };
    getOptions = () => {
        const {data} = this.state;
        return {
            tooltip: {
                trigger: 'item',
                formatter: '{a} <br/>{b} : {c} ({d}%)'
            },
            legend: {
                orient: 'vertical',
                left: 'left',
                data: ['', '', '', ''],
            },
            series: [
                {
                    name: '',
                    type: 'pie',
                    radius: '55%',
                    center: ['50%', '60%'],
                    data: [
                        {value: data[0], name: ''},
                        {value: data[1], name: ''},
                        {value: data[2], name: ''},
                        {value: data[3], name: ''},
                    ],
                    emphasis: {
                        itemStyle: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    }
                }
            ]
        };
    };

    render() {
        return (
            <Card title="">
                <ReactEcharts option={this.getOptions()}></ReactEcharts>
            </Card>
        );
    }
}
