import React from "react";
import {Card, Form, Input, Select, Button, Divider, message, Switch} from 'antd';
import Moment from "moment";


import LikeUploadImg from "../../../components/LikeUploadImg";
import RichTextEditor from "../../../components/rich-text-editor";
import {getJobPre, getJobFamily, addJob, editJob} from "../../../api/lifejobApi";
import config from "../../../config/config";
import {getUser} from "../../../api/adminApi";
import {getObj} from "../../../tools/cache-tool";

const {Item} = Form;
const {Option} = Select;

export default class AddEditLife extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            // 
            job_pre: [],
            job_family: [],
            // form
            id: '',
            job_content: '',
            imageUrl: '',
            focusImgUrl: '',
        };
        this.lifejobFormRef = React.createRef();
        this.lifejob_ref = React.createRef();
    }

    componentDidMount() {
        // 0.
        if (getObj('lifejob_tag') === 'edit' && !this.props.location.state) {
            this.props.history.goBack();
        }
        // 1. 
        if (this.props.location.state) {
            const jobItem = this.props.location.state.job;
            if (jobItem) {
                this.lifejobFormRef.current.setFieldsValue(jobItem);
                this.setState({
                    id: jobItem.id,
                    job_content: jobItem.job_content,
                    imageUrl: jobItem.job_img,
                    focusImgUrl: jobItem.focus_img,
                });
            }
        }
        getJobPre().then(result => {
            if (result && result.status === 1) {
                this.setState({
                    job_pre: result.data,
                });
            }
        }).catch((error) => {
            console.log(error);
        });
        getJobFamily().then(result => {
            if (result && result.status === 1) {
                this.setState({
                    job_family: result.data,
                });
            }
        }).catch((error) => {
            console.log(error);
        });
    }

    // state
    componentWillUnmount() {
        this.setState = (state, callback) => {
            return false;
        }
    }

    render() {
        const {imageUrl, focusImgUrl, job_pre, job_family, id} = this.state;
        const formItemLayout = {
            labelCol: {
                xs: {span: 4},
            },
            wrapperCol: {
                xs: {span: 12},
            }
        };
        const onFinish = (values) => {
            const {imageUrl, focusImgUrl} = this.state;
            if (!imageUrl) return message.warn('');
            let {
                job_name,
                job_author,
                job_pre_edu_id,
                job_family_edu_id,
                is_focus,
            } = values;
            let job_time = Moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
            // 
            let job_content = this.lifejob_ref.current.getContent();
            // API
            if (id) {
                editJob(
                    getUser().token,
                    id,
                    job_name,
                    imageUrl,
                    job_author,
                    job_time,
                    job_content,
                    job_pre_edu_id,
                    job_family_edu_id,
                    is_focus ? '1' : '0',
                    focusImgUrl,
                ).then(result => {
                    if (result?.status === 1) {
                        message.success('');
                        this.props.history.goBack();
                    }
                }).catch(() => {
                    message.error('');
                });
            } else {
                addJob(
                    getUser().token,
                    job_name,
                    imageUrl,
                    job_author,
                    job_time,
                    job_content,
                    job_pre_edu_id,
                    job_family_edu_id,
                    is_focus ? 1 : 0,
                    focusImgUrl,
                ).then(result => {
                    if (result?.status === 1) {
                        message.success('');
                        this.props.history.goBack();
                    }
                }).catch(() => {
                    message.error('');
                });
            }
        };
        return (
            <Card title={getObj('lifejob_tag') === 'add' ? "" : ""}>
                <Form {...formItemLayout} onFinish={onFinish} ref={this.lifejobFormRef}>
                    <Item label={""} name={"job_name"} rules={[{required: true, message: ''}]}>
                        <Input/>
                    </Item><Item label={""} name={"job_author"} rules={[{required: true, message: ''}]}>
                    <Input/>
                </Item>
                    <Item label={""} name={"job_pre_edu_id"} rules={[{required: true, message: ''}]}>
                        <Select style={{width: 200}} placeholder={""}>
                            {job_pre.map(item => (
                                <Option key={item.id} value={item.id}>{item.pre_edu_name}</Option>
                            ))}
                        </Select>
                    </Item>
                    <Item label={""} name={"job_family_edu_id"} rules={[{required: true, message: ''}]}>
                        <Select style={{width: 200}} placeholder={""}>
                            {job_family.map(item => (
                                <Option key={item.id} value={item.id}>{item.job_family_name}</Option>
                            ))}
                        </Select>
                    </Item>
                    <Item label={""} name={"job_img"}>
                        <LikeUploadImg upLoadBtnTitle={""} upLoadName={"lifejob_img"}
                                       upLoadAction={"/api/auth/lifejob/upload_lifejob"}
                                       upLoadImg={imageUrl ? config.BASE_URL + imageUrl : ''}
                                       successCallBack={(name) => {
                                           this.setState({imageUrl: name})
                                       }}/>
                    </Item>
                    <Item label={""} name={"focus_img"}>
                        <LikeUploadImg upLoadBtnTitle={""} upLoadName={"lifejob_img"}
                                       upLoadAction={"/api/auth/lifejob/upload_lifejob"}
                                       upLoadImg={focusImgUrl ? config.BASE_URL + focusImgUrl : ''}
                                       successCallBack={(name) => {
                                           this.setState({focusImgUrl: name})
                                       }}/>
                    </Item>
                    <Item label={""} name={"is_focus"} valuePropName={"checked"}>
                        <Switch checkedChildren="" unCheckedChildren="" disabled={!focusImgUrl}/>
                    </Item>
                    <Item label={""} name={"job_content"} wrapperCol={{span: 20}}>
                        <RichTextEditor uploadName={"lifejob_img"} upLoadAction={"/api/auth/lifejob/upload_lifejob"}
                                        htmlContent={this.state.job_content}
                                        ref={this.lifejob_ref}/>
                    </Item>
                    <Item wrapperCol={{span: 20}}>
                        <div style={{textAlign: 'center', marginTop: 30}}>
                            <Button type={"primary"}
                                    htmlType={"submit"}>{getObj('lifejob_tag') === 'add' ? "" : ""}</Button>
                            <Divider type={"vertical"}/>
                            <Button onClick={() => this.props.history.goBack()}></Button>
                        </div>
                    </Item>

                </Form>
            </Card>
        );
    }
}
