import React from 'react';
import PropTypes from 'prop-types';
// 
import BraftEditor from 'braft-editor';
// 
import 'braft-editor/dist/index.css';
import {Upload, Button} from "antd";
import {ContentUtils} from 'braft-utils'
import config from "../config/config";

export default class RichTextEditor extends React.Component {
    static propTypes = {
        uploadName: PropTypes.string.isRequired,
        upLoadAction: PropTypes.string.isRequired,
        htmlContent: PropTypes.string,
    };
    state = {
        // editorState
        editorState: BraftEditor.createEditorState(null)
    };
    UNSAFE_componentWillReceiveProps(nextProps, nextContext) {
        if (nextProps.htmlContent) {
            this.setState({
                editorState: BraftEditor.createEditorState(nextProps.htmlContent),
            });
        }
    }

    getContent = () => {
        return this.state.editorState.toHTML();
    };
    submitContent = async () => {
        // ctrl+s
        // editorState.toHTML()HTML
        // const htmlContent = this.state.editorState.toHTML()
        // const result = await saveEditorContent(htmlContent)
    };

    handleEditorChange = (editorState) => {
        this.setState({editorState})
    };
    editorControls = [
        'undo', 'redo', 'separator',
        'font-size', 'line-height', 'letter-spacing', 'separator',
        'text-color', 'bold', 'italic', 'underline', 'strike-through', 'separator',
        'superscript', 'subscript', 'remove-styles', 'emoji', 'separator', 'text-indent', 'text-align', 'separator',
        'headings', 'list-ul', 'list-ol', 'blockquote', 'code', 'separator',
        'link', 'separator', 'hr', 'separator', 'separator', 'clear'
    ];
    uploadHandler = (info) => {
        if (info.file.status === 'uploading') {
            this.setState({loading: true});
            return;
        }
        if (info.file.response && info.file.status === 'done' && info.file.response.status === 1) {
            const name = info.file.response.data.name;
            this.setState({
                editorState: ContentUtils.insertMedias(this.state.editorState, [{
                    type: 'IMAGE',
                    url: config.BASE_URL + name,
                }]),
            });
        }
    };
    extendControls = [
        {
            key: 'antd-uploader', // 
            type: 'component',
            component: (
                <Upload action={this.props.upLoadAction} name={this.props.uploadName} onChange={this.uploadHandler} showUploadList={false}>
                    <Button type={"button"} className={"control-item button upload-button"}
                            data-title={""}></Button>
                </Upload>
            )
        }
    ];

    render() {
        const {editorState} = this.state;
        return (
            <div className="my-component">
                <BraftEditor
                    controls={this.editorControls}
                    extendControls={this.extendControls}
                    value={editorState}
                    onChange={this.handleEditorChange}
                    onSave={this.submitContent}
                    style={{border: '1px solid lightgray'}}
                />
            </div>
        );
    }
}
