import ajax from './index';

/*
*   1. 
* */
export const getJobPre = () => ajax('/api/auth/lifejob/job_pre');
/*
*   2. 
* */
export const getJobFamily = () => ajax('/api/auth/lifejob/job_family');
/*
*   3. 
* */
export const addJob = (
    token,
    job_name,
    job_img,
    job_author,
    job_time,
    job_content,
    job_pre_edu_id,
    job_family_edu_id,
    is_focus,
    focus_img,
) => ajax('/api/auth/lifejob/add', {
    token,
    job_name,
    job_img,
    job_author,
    job_time,
    job_content,
    job_pre_edu_id,
    job_family_edu_id,
    is_focus,
    focus_img,
}, 'post');
/*
*   4. 
* */
export const getJobList = (page_num, page_size) => ajax('/api/auth/lifejob/list', {page_num, page_size});
/*
*   5. 
* */
export const setFocusJob = (id, is_focus) => ajax('/api/auth/lifejob/set_focus_job', {id, is_focus});
/*
*   6. 
* */
export const deleteJob = (id) => ajax('/api/auth/lifejob/delete_job', {id});
/*
*   7. 
* */
export const editJob = (
    token,
    id,
    job_name,
    job_img,
    job_author,
    job_time,
    job_content,
    job_pre_edu_id,
    job_family_edu_id,
    is_focus,
    focus_img
) => ajax('/api/auth/lifejob/edit', {
    token,
    id,
    job_name,
    job_img,
    job_author,
    job_time,
    job_content,
    job_pre_edu_id,
    job_family_edu_id,
    is_focus,
    focus_img
}, 'post');
