/*
*   6. 
* */
export const addResource = (
    token,
    resource_name,
    resource_author,
    resource_publish_time,
    resource_content,
    resource_category_id,
    resource_classes_id,
    resource_area_id,
    resource_mate_id,
    resource_format_id,
    resource_img,
    resource_price,
    is_focus,
    focus_img,
) => ajax('/api/auth/resource/add', {
    token,
    resource_name,
    resource_author,
    resource_publish_time,
    resource_content,
    resource_category_id,
    resource_classes_id,
    resource_area_id,
    resource_mate_id,
    resource_format_id,
    resource_img,
    resource_price,
    is_focus,
    focus_img,
}, 'post');
