import React from "react";
import {Card, Form, Input, Select, Button, Divider, DatePicker, message, Switch} from 'antd';
import Moment from "moment";

import LikeUploadImg from "../../../components/LikeUploadImg";
import config from "../../../config/config";
import {addLive, getLivePerson, getLiveTheme} from '../../../api/liveApi';
import {getUser} from "../../../api/adminApi";

const {Item} = Form;
const {Option} = Select;
const {RangePicker} = DatePicker;


export default class AddLive extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            imageUrl: '',
            focusImgUrl: '',
            live_theme: [],
            live_person: [],
        }
    }

    componentDidMount() {
        getLivePerson().then(result => {
            if (result && result.status === 1) {
                this.setState({
                    live_person: result.data,
                });
            }
        }).catch(error => {
            console.log(error);
        });
        getLiveTheme().then(result => {
            if (result && result.status === 1) {
                this.setState({
                    live_theme: result.data,
                });
            }
        }).catch(error => {
            console.log(error);
        });
    }

    render() {
        const {imageUrl, focusImgUrl, live_person, live_theme} = this.state;
        const formItemLayout = {
            labelCol: {xs: {span: 4},},
            wrapperCol: {xs: {span: 12},}
        }

        const onFinish = (values) => {
            console.log(values);
            const {imageUrl, focusImgUrl} = this.state;
            const {live_title, live_author, live_price, live_person_id, live_theme_id, is_focus, live_url} = values;
            if (!imageUrl) return message.warning('');
            // 
            const live_begin_time = Moment(values.live_time[0]).format('YYYY-MM-DD HH:mm:ss');
            const live_end_time = Moment(values.live_time[1]).format('YYYY-MM-DD HH:mm:ss');
            // 
            addLive(getUser().token, live_title, live_author, imageUrl, live_begin_time, live_end_time, live_price, live_person_id, live_theme_id, is_focus ? 1 : 0, focusImgUrl, live_url).then(result=>{
                if (result && result.status === 1) {
                    message.success(result.msg);
                    this.props.history.goBack();
                }
            }).catch(()=>{
                message.error('');
            })

        }
        return (
            <Card title={""}>
                <Form {...formItemLayout} onFinish={onFinish}>
                    <Item label={""} name={"live_title"} rules={[{required: true, message: ''}]}>
                        <Input/>
                    </Item>
                    <Item label={""} name={"live_author"} rules={[{required: true, message: ''}]}>
                        <Input/>
                    </Item>
                    <Item label={""} name={"live_price"} rules={[{required: true, message: ''}]}>
                        <Input type={"number"}/>
                    </Item>
                    <Item label={""} name={"live_time"} rules={[{required: true, message: ''}]}>
                        <RangePicker showTime/>
                    </Item>
                    <Item wrapperCol={{span: 5}} label={""} name={"live_person_id"}
                          rules={[{required: true, message: ''}]}>
                        <Select placeholder={""}>
                            {live_person.map(item => {
                                return (
                                    <Option value={item.id} key={item.id}>{item.live_person_name}</Option>
                                );
                            })}
                        </Select>
                    </Item>
                    <Item wrapperCol={{span: 5}} label={""} name={"live_theme_id"}
                          rules={[{required: true, message: ''}]}>
                        <Select placeholder={""}>
                            {live_theme.map(item => {
                                return (
                                    <Option value={item.id} key={item.id}>{item.live_theme_title}</Option>
                                );
                            })}
                        </Select>
                    </Item>
                    <Item label={""} name={"live_url"} rules={[{required: true, message: ''}]}>
                        <Input/>
                    </Item>
                    <Item label={""} name={"live_img"}>
                        <LikeUploadImg upLoadBtnTitle={""} upLoadName={"live_img"}
                                       upLoadAction={"/api/auth/live/upload_live"}
                                       upLoadImg={imageUrl ? config.BASE_URL + imageUrl : ''}
                                       successCallBack={(name) => {
                                           message.success('');
                                           this.setState({imageUrl: name});
                                       }}/>
                    </Item>
                    <Item label={""} name={"focus_img"}>
                        <LikeUploadImg upLoadBtnTitle={""} upLoadName={"live_img"}
                                       upLoadAction={"/api/auth/live/upload_live"}
                                       upLoadImg={focusImgUrl ? config.BASE_URL + focusImgUrl : ''}
                                       successCallBack={(name) => {
                                           message.success('');
                                           this.setState({focusImgUrl: name});
                                       }}/>
                    </Item>
                    <Item label={""} name={"is_focus"} valuePropName={"checked"}>
                        <Switch checkedChildren="" unCheckedChildren="" disabled={!focusImgUrl}/>
                    </Item>
                    <Item wrapperCol={{span: 20}}>
                        <div style={{textAlign: 'center', marginTop: 30}}>
                            <Button type={"primary"} htmlType="submit"></Button>
                            <Divider type={"vertical"}/>
                            <Button onClick={() => this.props.history.goBack()}></Button>
                        </div>
                    </Item>
                </Form>
            </Card>
        );
    }
}
