import React, {Component} from "react";
import {connect} from 'react-redux';
import {Form, Input, Button, message} from 'antd';
import {UserOutlined, LockOutlined} from '@ant-design/icons';
import md5 from 'blueimp-md5';

import './css/login.css';
import xl from './images/xiaoliao.png';
import {checkLogin, saveUser} from '../../api/adminApi';
import config from '../../config/config';

class Login extends Component {
    render() {
        const onFinish = values => {
            // MD5
            const hash_pwd = md5(values.password, config.KEY);
            // 
            checkLogin(values.account, hash_pwd).then((result) => {
                if (result && result.status === 1) {
                    // 
                    message.success(result.msg);
                    saveUser(result.data);
                    // 
                    this.props.history.replace('/');
                } else if (result && result.status === 0) {
                    message.warning(result.msg);
                } else {
                    message.error('');
                }
            }).catch((error) => {
                message.error('');
            });
        };
        return (
            <div className="login">
                <div className="login-wrap">
                    <div className="avatar">
                        <img src={xl} alt=""/>
                    </div>
                    <div className="content">
                        <Form
                            name="normal_login"
                            className="login-form"
                            initialValues={{remember: true}}
                            onFinish={onFinish}
                        >
                            <Form.Item name="account" rules={[{required: true, message: '!'}]}>
                                <Input prefix={<UserOutlined className="site-form-item-icon"/>} placeholder=""/>
                            </Form.Item>
                            <Form.Item name="password" rules={[{required: true, message: '!'}]}>
                                <Input
                                    prefix={<LockOutlined className="site-form-item-icon"/>}
                                    type="password"
                                    placeholder=""
                                />
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="submit" className="login-form-button"></Button>
                            </Form.Item>
                        </Form>
                    </div>
                </div>
            </div>
        );
    }
}

export default connect(null, null)(Login)
