import React from "react";
import {Card, Button, Table, Switch, Divider, Modal, message, notification} from 'antd';
import {deleteActivities, getActivitiesList, setFocusActivities} from "../../../api/activitiesApi";
import config from "../../../config/config";

export default class ActivitiesList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            activitiesList: [],
            totalSize: 0,
            pageSize: 4,
        }
    }

    componentDidMount() {
        this._loadData();
    }

    _loadData = (page_num = 1, page_size = 4) => {
        getActivitiesList(page_num, page_size).then(result => {
            if (result && result.status === 1) {
                message.success(result.msg);
                this.setState({
                    activitiesList: result.data.activities_list,
                    totalSize: result.data.activities_count,
                })
            }
        }).catch(() => {
            message.error('');
        });
    };

    // 
    columns = [
        {title: 'ID', dataIndex: 'id', width: 50, align: 'center'},
        {title: '', dataIndex: 'activities_name', align: 'center'},
        {title: '', dataIndex: 'activities_time', align: 'center'},
        {
            title: '', dataIndex: 'activities_img', render: (text) => {
                return (
                    <img src={config.BASE_URL + text} alt="" style={{width: 100}}/>
                );
            }, align: 'center'
        },
        {title: '', dataIndex: 'activities_price', align: 'center'},
        {title: '', dataIndex: 'activities_bus_day_id', align: 'center'},
        {
            title: '', dataIndex: 'is_focus', render: (text, record) => {
                return (
                    <Switch checkedChildren={""} unCheckedChildren={""} defaultChecked={text === 1}
                            disabled={!record.focus_img} onChange={checked => {
                        setFocusActivities(record.id, checked ? 1 : 0).then(result => {
                            if (result && result.status === 1) {
                                notification["success"]({
                                    message: `: ${record.activities_name}`,
                                    description: `${checked ? '' : ''}`,
                                });
                                record.is_focus = checked ? 1 : 0;
                            }
                        });
                    }}/>
                );
            }, align: 'center'
        },
        {
            title: '', align: 'center', render: (text, record) => {
                return (
                    <div>
                        <Button onClick={() => {
                            this.props.history.push({
                                pathname: '/activities/edit-activities',
                                state: {
                                    activities: record,
                                },
                            });
                        }}></Button>
                        <Divider type="vertical"/>
                        <Button onClick={() => {
                            Modal.confirm({
                                title: '',
                                content: '',
                                okText: '',
                                cancelText: '',
                                onOk: () => {
                                    deleteActivities(record.id).then(result => {
                                        if (result && result.status === 1) {
                                            message.success(result.msg);
                                            this._loadData(this.state.pageNum, this.state.pageSize);
                                        } else {
                                            message.error('');
                                        }
                                    }).catch(() => {
                                        message.error('');
                                    });
                                }
                            });
                        }}></Button>
                    </div>
                );
            }
        },
    ];

    render() {
        // 
        let AddBtn = (
            <Button type={"primary"} onClick={() => {
                this.props.history.push('/activities/add-activities');
            }}></Button>
        );
        return (
            <div>
                <Card title={""} extra={AddBtn}>
                    <Table rowKey={'id'} columns={this.columns} dataSource={this.state.activitiesList} pagination={{
                        total: this.state.totalSize,
                        pageSize: this.state.pageSize,
                        onChange: (pageNum, pageSize) => {
                            this._loadData(pageNum);
                        }
                    }}>
                    </Table>
                </Card>
            </div>
        );
    }
}
