import React from "react";
import {Card, Form, Input, Button, Divider, DatePicker, Select, message, Switch} from 'antd';
import Moment from 'moment';

import RichTextEditor from "../../../components/rich-text-editor";
import LikeTag from '../../../components/LikeTag';
import LikeUploadImg from "../../../components/LikeUploadImg";
import config from "../../../config/config";
import {
    getActivitiesAddress,
    getActivitiesObject,
    getActivitiesBus,
    addActivities
} from '../../../api/activitiesApi';
import {getUser} from "../../../api/adminApi";

const {Item} = Form;
const {Option} = Select;

export default class AddActivities extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            imageUrl: '',
            focusImgUrl: '',
            activities_address: [],
            activities_object: [],
            activities_bus: [],
            tags: [],
        };
        this.activities_intro_ref = React.createRef();
        this.activities_trip_ref = React.createRef();
        this.activities_days_ref = React.createRef();
        this.activities_notice_ref = React.createRef();
    }

    componentDidMount() {
        getActivitiesBus().then(result => {
            if (result && result.status === 1) {
                this.setState({
                    activities_bus: result.data,
                });
            }
        }).catch((error) => {
            console.log(error);
        });
        getActivitiesObject().then(result => {
            if (result && result.status === 1) {
                this.setState({
                    activities_object: result.data,
                });
            }
        }).catch((error) => {
            console.log(error);
        });
        getActivitiesAddress().then(result => {
            if (result && result.status === 1) {
                this.setState({
                    activities_address: result.data,
                });
            }
        }).catch((error) => {
            console.log(error);
        });
    }

    render() {
        const formItemLayout = {
            labelCol: {
                xs: {span: 4},
            },
            wrapperCol: {
                xs: {span: 12},
            }
        };
        const {imageUrl, focusImgUrl, activities_address, activities_object, activities_bus} = this.state;
        const onFinish = (values) => {
            console.log(values);
            const {imageUrl, focusImgUrl, tags} = this.state;
            if (!imageUrl) return message.warn('');
            const activities_time = Moment(values.activities_time).format('YYYY-MM-DD HH:mm:ss');
            const {activities_name, activities_price, activities_address_id, activities_object_id, activities_bus_day_id, activities_count, is_focus} = values;
            let tagStr = tags.join(',');
            // 
            let activities_intro = this.activities_intro_ref.current.getContent();
            let activities_trip = this.activities_trip_ref.current.getContent();
            let activities_day = this.activities_days_ref.current.getContent();
            let activities_notice = this.activities_notice_ref.current.getContent();
            console.log(activities_intro);
            // API
            addActivities(getUser().token, activities_name, activities_time, imageUrl, activities_price, tagStr, activities_count, activities_address_id, activities_object_id, activities_bus_day_id, activities_intro, activities_trip, activities_day, activities_notice, is_focus ? 1 : 0, focusImgUrl).then(result=>{
                console.log(result);
                if (result?.status === 1) {
                    message.success('');
                    this.props.history.goBack();
                }
            }).catch(()=>{
                message.error('');
            })
        };
        return (
            <Card title={""}>
                <Form {...formItemLayout} onFinish={onFinish}>
                    <Item label={""} name={"activities_name"} rules={[{required: true, message: ''}]}>
                        <Input/>
                    </Item>
                    <Item label={""} name={"activities_time"} rules={[{required: true, message: ''}]}>
                        <DatePicker/>
                    </Item>
                    <Item label={""} name={"activities_price"} rules={[{required: true, message: ''}]}>
                        <Input type={"number"}/>
                    </Item>
                    <Item label={""} name={"activities_count"} rules={[{required: true, message: ''}]}>
                        <Input type={"number"}/>
                    </Item>
                    <Item label={""} name={"activities_address_id"} rules={[{required: true, message: ''}]}>
                        <Select style={{width: 200}} placeholder={""}>
                            {activities_address.map(item => (
                                <Option key={item.id} value={item.id}>{item.activities_address}</Option>
                            ))}
                        </Select>
                    </Item>
                    <Item label={""} name={"activities_object_id"} rules={[{required: true, message: ''}]}>
                        <Select style={{width: 200}} placeholder={""}>
                            {activities_object.map(item => (
                                <Option key={item.id} value={item.id}>{item.activities_object_name}</Option>
                            ))}
                        </Select>
                    </Item>
                    <Item label={""} name={"activities_bus_day_id"} rules={[{required: true, message: ''}]}>
                        <Select style={{width: 200}} placeholder={""}>
                            {activities_bus.map(item => (
                                <Option key={item.id} value={item.id}>{item.activities_bus_day}</Option>
                            ))}
                        </Select>
                    </Item>
                    <Item label={""}>
                        <LikeTag tagsCallBack={(tags) => {
                            this.setState({tags});
                        }}/>
                    </Item>
                    <Item label={""} name={"activities_img"}>
                        <LikeUploadImg upLoadBtnTitle={""} upLoadName={"activities_img"}
                                       upLoadAction={"/api/auth/activities/upload_activities"}
                                       upLoadImg={imageUrl ? config.BASE_URL + imageUrl : ''}
                                       successCallBack={(name) => {
                                           message.success('');
                                           this.setState({imageUrl: name});
                                       }}/>
                    </Item>
                    <Item label={""} name={"focus_img"}>
                        <LikeUploadImg upLoadBtnTitle={""} upLoadName={"activities_img"}
                                       upLoadAction={"/api/auth/activities/upload_activities"}
                                       upLoadImg={focusImgUrl ? config.BASE_URL + focusImgUrl : ''}
                                       successCallBack={(name) => {
                                           message.success('');
                                           this.setState({focusImgUrl: name});
                                       }}/>
                    </Item>
                    <Item label={""} name={"is_focus"} valuePropName={"checked"}>
                        <Switch checkedChildren="" unCheckedChildren="" disabled={!focusImgUrl}/>
                    </Item>
                    <Item label={""} name={"activities_intro"} wrapperCol={{span: 20}}>
                        <RichTextEditor ref={this.activities_intro_ref} upLoadAction={"/api/auth/activities/upload_activities"} uploadName={"activities_img"}/>
                    </Item>
                    <Item label={""} name={"activities_trip"} wrapperCol={{span: 20}}>
                        <RichTextEditor ref={this.activities_trip_ref} upLoadAction={"/api/auth/activities/upload_activities"} uploadName={"activities_img"}/>
                    </Item>
                    <Item label={""} name={"activities_days"} wrapperCol={{span: 20}}>
                        <RichTextEditor ref={this.activities_days_ref} upLoadAction={"/api/auth/activities/upload_activities"} uploadName={"activities_img"}/>
                    </Item>
                    <Item label={""} name={"activities_notice"} wrapperCol={{span: 20}}>
                        <RichTextEditor ref={this.activities_notice_ref} upLoadAction={"/api/auth/activities/upload_activities"} uploadName={"activities_img"}/>
                    </Item>
                    <Item wrapperCol={{span: 20}}>
                        <div style={{textAlign: 'center', marginTop: 30}}>
                            <Button type={"primary"} htmlType={"submit"}></Button>
                            <Divider type={"vertical"}/>
                            <Button onClick={() => this.props.history.goBack()}></Button>
                        </div>
                    </Item>
                </Form>
            </Card>
        );
    }
}
