import React from "react";
import {Card, Button, Table, Switch, Divider, Modal, message, notification} from 'antd';
import {getLive, deleteLive, setFocusLive} from "../../../api/liveApi";
import config from "../../../config/config";

export default class LivesList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            liveList: [],
            totalSize: 0,
            pageSize: 4,
            pageNum: 1,
        }
    }

    componentDidMount() {
        this._loadData();
    }

    _loadData = (page_num = 1, page_size = 4) => {
        getLive(page_num, page_size).then(result => {
            if (result && result.status === 1) {
                message.success(result.msg);
                this.setState({
                    liveList: result.data.list,
                    totalSize: result.data.live_count,
                })
            }
        }).catch(() => {
            message.error('');
        });
    };
    // 
    columns = [
        {title: 'ID', dataIndex: 'id', width: 50, align: 'center'},
        {title: '', dataIndex: 'live_title', align: 'center'},
        {
            title: '', dataIndex: 'live_img', render: (text) => {
                return (
                    <img src={config.BASE_URL + text} alt="" style={{width: 100}}/>
                );
            }, align: 'center'
        },
        {title: '', dataIndex: 'live_begin_time', align: 'center'},
        {title: '', dataIndex: 'live_author', align: 'center'},
        {title: '', dataIndex: 'live_price', align: 'center'},
        {
            title: '', dataIndex: 'is_focus', align: 'center', render: (text, record) => {
                return (
                    <Switch checkedChildren={""} unCheckedChildren={record.focus_img ? '' : ''}
                            defaultChecked={text === 1} disabled={!record.focus_img} onChange={(checked) => {
                        setFocusLive(record.id, checked ? 1 : 0).then(result => {
                            if (result && result.status === 1) {
                                notification['success']({
                                    message: `:${record.live_title}`,
                                    description: `${checked ? '' : ''}`,
                                });
                                record.is_focus = checked ? 1 : 0;
                            }
                        });
                    }}/>);
            }
        }, {
            title: '', align: 'center', render: (text, record) => {
                return (
                    <div>
                        <Button onClick={() => {
                            this.props.history.push({
                                pathname: '/live/edit-live',
                                state: {
                                    live: record
                                }
                            });
                        }}></Button>
                        <Divider type="vertical"/>
                        <Button onClick={() => {
                            Modal.confirm({
                                title: '',
                                content: '',
                                okText: '',
                                cancelText: '',
                                onOk: () => {
                                    deleteLive(record.id).then(result => {
                                        if (result && result.status === 1) {
                                            message.success(result.msg);
                                            this._loadData(this.state.pageNum, this.state.pageSize);
                                        } else {
                                            message.error('');
                                        }
                                    }).catch(() => {
                                        message.error('');
                                    })
                                }
                            });
                        }}></Button>
                    </div>
                );
            }
        },
    ];

    render() {
        // 
        let AddBtn = (
            <Button type={"primary"} onClick={() => {
                this.props.history.push('/live/add-live');
            }}></Button>
        );
        return (
            <div>
                <Card title={""} extra={AddBtn}>
                    <Table rowKey={'id'} columns={this.columns} dataSource={this.state.liveList} pagination={{
                        total: this.state.totalSize,
                        pageSize: this.state.pageSize,
                        onChange: (pageNum, pageSize) => this._loadData(pageNum)
                    }}>
                    </Table>
                </Card>
            </div>
        );
    }
}
