import React from "react";
import {Card, Form, Input, Button, Divider, message} from 'antd';


import LikeUploadImg from "../../../components/LikeUploadImg";
import config from "../../../config/config";
import {getUser} from "../../../api/adminApi";
import {getObj} from "../../../tools/cache-tool";
import {editWebSite, getWebSiteInfo} from "../../../api/homeApi";

const {Item} = Form;
const {TextArea} = Input;

export default class HomeCommon extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            siteLogoUrl: '',
        };
        this.homeFormRef = React.createRef();
    }

    componentDidMount() {
        // 
        getWebSiteInfo().then(result => {
            if (result && result.status === 1) {
                const homeItem = result.data;
                this.homeFormRef.current.setFieldsValue(homeItem);
                this.setState({
                    siteLogoUrl: homeItem.site_logo,
                });
            }
        }).catch(error => {
            message.error('');
        });
    }

    // state
    componentWillUnmount() {
        this.setState = (state, callback) => {
            return false;
        }
    }

    render() {
        const {siteLogoUrl} = this.state;
        const formItemLayout = {
            labelCol: {
                xs: {span: 4},
            },
            wrapperCol: {
                xs: {span: 12},
            }
        };
        const onFinish = (values) => {
            const {siteLogoUrl} = this.state;
            if (!siteLogoUrl) return message.warn('LOGO');
            let {
                site_name,
                site_keyword,
                site_des,
                site_copy,
                site_bei,
            } = values;
            editWebSite(
                getUser().token,
                site_name,
                site_keyword,
                site_des,
                siteLogoUrl,
                site_copy,
                site_bei,
            ).then(result => {
                if (result && result.status === 1) {
                    message.success(result.msg);
                    this.props.history.goBack();
                }
            }).catch(error => {
                message.error('');
            });
        };
        return (
            <Card title={getObj('lifejob_tag') === 'add' ? "" : ""}>
                <Form {...formItemLayout} onFinish={onFinish} ref={this.homeFormRef}>
                    <Item label={""} name={"site_name"} rules={[{required: true, message: ''}]}>
                        <Input placeholder={""}/>
                    </Item>
                    <Item label={""} name={"site_keyword"} rules={[{required: true, message: ''}]}>
                        <TextArea rows={2} placeholder={""}/>
                    </Item>
                    <Item label={""} name={"site_des"} rules={[{required: true, message: ''}]}>
                        <TextArea rows={4} placeholder={""}/>
                    </Item>

                    <Item label={"LOGO"} name={"site_logo"}>
                        <LikeUploadImg upLoadBtnTitle={"LOGO"} upLoadName={"site_logo"}
                                       upLoadAction={"/api/auth/home/upload_home"}
                                       upLoadImg={siteLogoUrl ? config.BASE_URL + siteLogoUrl : ''}
                                       successCallBack={(name) => {
                                           this.setState({siteLogoUrl: name})
                                       }}/>
                    </Item>
                    <Item label={""} name={"site_copy"} rules={[{required: true, message: ''}]}>
                        <Input placeholder={""}/>
                    </Item>
                    <Item label={""} name={"site_bei"} rules={[{required: true, message: ''}]}>
                        <Input placeholder={""}/>
                    </Item>
                    <Item wrapperCol={{span: 20}}>
                        <div style={{textAlign: 'center', marginTop: 30}}>
                            <Button type={"primary"}
                                    htmlType={"submit"}></Button>
                            <Divider type={"vertical"}/>
                            <Button onClick={() => this.props.history.goBack()}></Button>
                        </div>
                    </Item>
                </Form>
            </Card>
        );
    }
}
